

header {
  background-color: #61dafb;
  display: flex;
  justify-content: space-between;
  height: 40px;
}

main {
  background: green;
  height: calc(100vh - 40px);
  width: 100%;
  display: flex;
}
.BarLeft {
width: 50%;
background-color: blueviolet;
height: 100%;
display: flex;
justify-content: center;
align-items: center;

}
.currentBar {
width: 80%;
height: 80%;
background-color: rgb(117, 117, 121);
overflow-y: auto;
position: relative;
display: flex;
flex-direction: column;
align-items: flex-start;
}

.Coin {

display: flex;
margin: 4px;
font-family: 'Courier New', Courier, monospace;
font-weight: 600;
align-items: center;
border-bottom:2px solid rgba(95, 95, 95, 0.685) ;
cursor: pointer;
}
.Coin_name {
  margin-right: 10px;
  font-size: 1.1em;
}
.Coin:hover {
box-shadow: 0px 0px 7px rgb(12, 14, 12);
}
.Coin span {
  margin:  0 4px;
  font-size: 1em;
}
.Coin_usd{
  color: rgb(60, 175, 80);
  font-weight: 800;
 
}
.Coin_vol {
  color: rgb(27, 27, 131);
}

.favorit {
  position: absolute;
  right: 0;
  width: 50px;
  height: 50px;
}
.favorit img {
  cursor: pointer;
}


.BarRight {
  width: 50%;
  background-color: rgb(37, 75, 70);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.CurrentD {
  display: flex;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 600;
  flex-direction: column;
  align-items: center;
  width: calc(95% - 4px);
  justify-content: space-between;
  border: 2px solid rgba(104, 104, 104, 0.541);
  padding: 10px;
}
.CurrentD p:nth-child(1){
  color: rgb(60, 175, 80);
}
.CurrentD p:nth-child(2){
  color: rgb(27, 27, 131);
}
.CurrentD p:nth-child(3){
  color: rgb(168, 11, 11);
}
.CurrentD p:nth-child(4){
  color: blueviolet;
}
@media (max-width:1000px) {

header {
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 65px;
}
main {
  height: calc(100vh - 65px);
  overflow-y: scroll;
  
  flex-direction: column;
}
.BarLeft , .BarRight {
  width: 100%;
  height: 50vh;
}
}